@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.va__blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.va__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.va__blog-container_article-image img {
    width: 100%;
    height: 100%;
}

.va__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.va__blog-container_article-content a {
    font-family: var(--font-family);
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;

    color: #fff;
}

.va__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
}

.va__blog-container_article-content p {
    font-family: var(--font-family);
    color: #fff;
    margin-bottom: 1rem;
}

.va__blog-container_article-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .va__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}
.va__blog {
    display: flex;
    flex-direction: column;    
}

.va__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.va__blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.va__blog-container {
    display: flex;
    flex-direction: row;
}

.va__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.va__blog-container_groupB {
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .va__blog-container {
        flex-direction: column-reverse;
    }


    .va__blog-container_groupA {
        margin: 2rem 0;
    }

    .va__blog-container_groupA .va__blog-container_article  {
       width: 48%;
    }

    .va__blog-container_groupA .va__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .va__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .va__blog-container_groupA .va__blog-container_article  {
       width: 100%;
    }

    .va__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .va__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.va__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.va__features-container__feature-title {
    flex: 1 1;
    max-width: 250px;
    margin-right: 2rem;
}

.va__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.va__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.va__features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.va__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 15px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and (max-width: 550px) {
    .va__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .va__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .va__features-container__feature {
        margin: 1rem 0;
    }
}
.va__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.va__features-heading {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.va__features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.va__features-heading a {
    color: var(--color-subtext);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.va__features-container {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .va__features {
        flex-direction: column;
    }

    .va__features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .vanpm __features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}
.va__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);
}

.va__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 3rem;
}

.va__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
}

.va__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;

    margin-bottom: 10rem;
    cursor: pointer;
}

.va__footer-btn a {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 21px;
    color: #fff;
    word-spacing: 2px;
}

.va__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    flex-wrap: wrap;

    width: 100%;
    text-align: left;
}

.va__footer-links div {
    width: 250px;
    margin: 1rem;
}

.va__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.va__footer-links_logo img {
    width: 200px;
    height: 200px;

    margin-bottom: 1rem;
}

.va__footer-links_logo h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.va__footer-links_logo p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

.va__footer-links_div address {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 18px;
    color: #fff;
}

.va__footer-links_div abbr {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 18px;
    color: #fff;
}

.va__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.va__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.va__footer-links_div p {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}

.va__footer-links_div a {
    font-size: 12px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}



@media screen and (max-width: 850px) {
    .va__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .va__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .va__footer-links div {
        margin: 1rem 0;
    }

    .va__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .va__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}
