}

.va__navbar-links_logo img{
    width: 51.56px;
    height: 50.02px;
}

.va__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.va__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.va__navbar-links_container p, 
.va__navbar-sign p,
.va__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.va__navbar-sign button,
.va__navbar-menu_container button{
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

 .va__navbar-menu {
    margin-left: 1rem;
    
    display: none;
    position: relative;
}

.va__navbar-menu svg {
    cursor: pointer;
}

.va__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.va__navbar-menu_container p {
    margin: 1rem 0;
}

.va__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .va__navbar-links_container {
        display: none;
    }    

    .va__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .va__navbar{
        padding: 2rem 4rem;
    }    
}

@media screen and (max-width: 550px) {
    .va__navbar{
        padding: 2rem;
    }    

    .va__navbar-sign{
        display: none;
    }

    .va__navbar-menu_container{
        top: 20px;
    }

    .va__navbar-menu_container-links-sign{
        display: block;
    }
} 
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {
    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
}

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
    -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }

    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 4rem 2rem;
    }

    .section__margin {
        margin: 4rem 2rem;
    }
}
